import { Stack, Typography, Button } from "@mui/material";
import officePic from "../assets/office3.jpg";
import HomeIcon from "@mui/icons-material/Home";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import GroupIcon from "@mui/icons-material/Group";
import NewspaperIcon from "@mui/icons-material/Newspaper";
import SearchIcon from "@mui/icons-material/Search";
import PersonIcon from "@mui/icons-material/Person";
import Diversity1Icon from "@mui/icons-material/Diversity1";
import { useNavigate } from "react-router-dom";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";
import FacebookIcon from "@mui/icons-material/Facebook";
import MailIcon from "@mui/icons-material/Mail";
import PhoneIcon from "@mui/icons-material/Phone";
import ArrowCircleUpIcon from "@mui/icons-material/ArrowCircleUp";

function Company() {
  const history = useNavigate();

  const handleHome = () => {
    console.log("Home button clicked");
    history("/Home");
  };

  const handleProducts = () => {
    console.log("Home button clicked");
    history("/Products");
  };

  const handleNews = () => {
    console.log("Home button clicked");
    history("/News");
  };


  return (
    <Stack>
      <Stack
        height={"100vh"}
        width={"100%"}
        sx={{
          backgroundImage: `url(${officePic})`,
          backgroundSize: "cover",
          backgroundRepeat: "no-repeat",
          // backgroundPosition: "center",
        }}
      >
        <Stack
          height={"100px"}
          direction={"row"}
          alignItems={"center"}
        >
          <Stack direction={"row"} width={'30%'} px={5}>
            <Diversity1Icon sx={{ color: "white" }} />
            <Typography color={"white"}>Heart Turnings</Typography>
          </Stack>
          <Stack
            width={"70%"}
            direction={"row"}
            justifyContent={"space-around"}
          >
            <Button
              onClick={handleHome}
              sx={{
                ":hover": {
                  bgcolor: "#1976d2",
                  color: "white",
                },
                textTransform: "none",
              }}
            >
              <Stack direction={"row"} spacing={1}>
                <HomeIcon sx={{ color: "white" }} />
                <Typography color={"white"}>Home</Typography>
              </Stack>
            </Button>
            <Button
              onClick={handleProducts}
              sx={{
                ":hover": {
                  bgcolor: "#1976d2",
                  color: "white",
                },
                textTransform: "none"
                // borderRadius: "30% 70% 70% 30% / 30% 30% 70% 70%"
              }}
            >
              <Stack direction={"row"}>
                <ShoppingCartIcon sx={{ color: "white" }} />
                <Typography color={"white"}>Products</Typography>
              </Stack>
            </Button>
            <Button
              sx={{
                ":hover": {
                  bgcolor: "#1976d2",
                  color: "white",
                },
                bgcolor: "#1976d2",
                color: "white",
                textTransform: "none",
              }}
            >
              <Stack direction={"row"}>
                <GroupIcon sx={{ color: "white" }} />
                <Typography color={"white"}>Company</Typography>
              </Stack>
            </Button>
            <Button
              onClick={handleNews}
              sx={{
                ":hover": {
                  bgcolor: "#1976d2",
                  color: "white",
                },
                textTransform: "none",
              }}
            >
              <Stack direction={"row"}>
                <NewspaperIcon sx={{ color: "white" }} />
                <Typography color={"white"}>News</Typography>
              </Stack>
            </Button>
            <Button
              sx={{
                ":hover": {
                  bgcolor: "#1976d2",
                  color: "white",
                },
                textTransform: "none",
              }}
            >
              <Stack direction={"row"}>
                <SearchIcon sx={{ color: "white" }} />
                <Typography color={"white"}>Search</Typography>
              </Stack>
            </Button>
            <Button
              sx={{
                ":hover": {
                  bgcolor: "#1976d2",
                  color: "white",
                },
                textTransform: "none",
              }}
            >
              <PersonIcon sx={{ color: "white" }} />
            </Button>
          </Stack>
        </Stack>
        <Stack mx={10} mt={20} width='50%'>
          <Typography
          fontFamily={"verdana"}
          fontSize={"35px"}
          // fontWeight={"bold"}
          color={"white"}
          >Lorem ipsum dolor sit amet Lorem ipsum dolor sit amet Lorem ipsum dolor sit amet</Typography>
        </Stack>
        <Stack direction={'row'} mx={10} justifyContent={'space-between'} mt={20}>
          <Button>Audio on</Button>
          <Button>Pause video</Button>
        </Stack>
      </Stack>
      <Stack height={"100vh"} justifyContent={'center'}>
        <Stack width={'90%'} height={'90%'} bgcolor={'grey'} alignSelf={'center'}></Stack>
      </Stack>
      <Stack height={"40vh"} bgcolor={"black"} direction={"row"}>
        <Stack width={"70%"} pl={5} justifyContent={"center"} spacing={5}>
          <Typography
            fontFamily={"verdana"}
            fontSize={"22px"}
            fontWeight={"bold"}
            color={"white"}
          >
            Paint that memory today <br /> to a permanent piece of art!
          </Typography>
          <Typography
            fontFamily={"verdana"}
            fontSize={"15px"}
            // fontWeight={"bold"}
            color={"grey"}
          >
            Copyright  Heart Turnings. All Rights Reserved
          </Typography>
        </Stack>
        <Stack width={"30%"} spacing={1} justifyContent={"center"}>
          <Typography
            fontFamily={"verdana"}
            // fontSize={"35px"}
            color={"grey"}
          >
            Home
          </Typography>
          <Typography
            fontFamily={"verdana"}
            // fontSize={"35px"}
            color={"grey"}
          >
            Products
          </Typography>
          <Typography
            fontFamily={"verdana"}
            // fontSize={"35px"}
            color={"grey"}
          >
            News
          </Typography>
          <Typography
            fontFamily={"verdana"}
            // fontSize={"35px"}
            color={"grey"}
          >
            Company
          </Typography>
          <Stack direction={"row"} spacing={1}>
            <InstagramIcon sx={{ color: "grey" }} />
            <TwitterIcon sx={{ color: "grey" }} />
            <FacebookIcon sx={{ color: "grey" }} />
            <MailIcon sx={{ color: "grey" }} />
            <PhoneIcon sx={{ color: "grey" }} />
            <ArrowCircleUpIcon sx={{ color: "grey" }} />
          </Stack>
        </Stack>
      </Stack>
    </Stack>
  );
}

export default Company;
